<template>
  <div id="add-blog">
    <h1></h1>
    <form action>
      <label></label>
      <input type="text" v-model="blog.title" required>

      <label></label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxs">
        <label></label>
        <input type="checkbox" value="" v-model="blog.categories">
        <label></label>
        <input type="checkbox" value="" v-model="blog.categories">
        <label></label>
        <input type="checkbox" value="" v-model="blog.categories">
      </div>

      <label></label>
      <select v-model="blog.author">
        <option v-for="(author,i) in authors" :key="i">{{author}}</option>
      </select>

      <button @click.prevent="post"></button>
    </form>

    <h1 v-if="submit"></h1>

    <hr>

    <div id="preview">
      <h1></h1>
      <h2>{{blog.title}}</h2>
      <h2></h2>
      <article>{{blog.content}}</article>
      <h2>:</h2>
      <ul v-for="(categories,i) in blog.categories" :key="i">
        <li>{{categories}}</li>
      </ul>
      <h2>{{blog.author}}</h2>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {},
      authors: ["admin", "user"],
      submit: false
    };
  },
  methods: {
    fetchData() {
      // 
      const query = Bmob.Query("blog_data");
      query
        .get(this.id)
        .then(res => {
          this.blog = res;
        })
        .catch(err => {
          console.log(err);
        });
    },

    // 
    post() {

      const query = Bmob.Query("blog_data");
      query
        .get(this.id)
        .then(res => {
          console.log(res);
          res.set("title", this.blog.title);
          res.set("content", this.blog.content);
          res.set("categories", this.blog.categories);
          res.set("author", this.blog.author);
          res.save();
          this.submit = true;
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  created() {
    this.fetchData();
  }
};
</script>

<style lang="scss" scoped>
</style>

